import { IFileHandleResponse } from "../fileHandler";
import { IParsedPath } from "../RouteParser";

export class NanoValidations{
    public ValidateUrl(path: IParsedPath): IFileHandleResponse | undefined{
        if (path.dir.indexOf("/..") > -1){
            //why 400 -> https://www.iana.org/assignments/http-status-codes/http-status-codes.xhtml [RFC7231, Section 6.5.1]
            //RFC7231 ->
            //https://datatracker.ietf.org/doc/html/rfc7231  Section 6.5.1 -> deceptive request routing
            return { status: 400, content: "deceptive request routing" };
        }

        return undefined;
    }
}